import React from 'react';
import styles from './VideoGuides.module.css';
import videos from './data/videos.json'; // Ensure the path is correct

export default function VideoGuides() {
  return (
    <div>
      <h3 className='heading text-left'>Video guides</h3>
      <div className='container'>
        {videos.map(video => (
          <div key={video.id} className={styles.videoContainer}>
            <p className={styles.videoTitle}>{video.title}</p>
            <iframe
              width='560'
              height='315'
              src={`https://www.youtube.com/embed/${video.id}`}
              title={video.title}
              frameBorder='0'
              allow='accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture'
              allowFullScreen
            ></iframe>
          </div>
        ))}
      </div>
    </div>
  );
}
